.movies-list-wrapper {
    margin: 0;
    padding: 0 0 10px;
    width: 100%;
    list-style: none;
    display: grid;
    grid-auto-rows: auto;
    grid-template-columns: repeat(5, minmax(240px, 1fr));
    gap: 30px 25px;
    justify-content: center;
}

